	export default {
        onLoad:function(option){
            this.getData(option.orderSn)
            this.orderSn=option.orderSn;
        },
        created(){
          // 为图片上传添加请求头
            uni.getStorage({
                key:'token',
                success:(res)=>{
                    this.headerData.token=res.data
                }
            })
        },
		data() {
			return {
                URL:getApp().globalData.URL,
				content:"",
                orderSn:null,
                headerData:{},
                filesArr:[],
                pic_list:[],
                data:[]
                
			};
		},
        methods:{
            // 请求页面数据
            async getData(orderSn){
                await this.$u.get('/get_task_order_info',{orderSn}).then(res=>{
                this.data=res.data    
                })
            },
            // 验证参数
            paramCheck(){
                if(this.$u.test.empty(this.filesArr)&&this.$u.test.empty(this.content)){
                    uni.showToast({
                        title:"图片和文字至少填写一项",
                        icon:"none"
                    })
                    return false;
                }
                return true;
            },
            async postOrder(){
                if(!this.paramCheck())return;
                // push 到要添加的订单队列中
                //response是服务器返回数据
                //将response 转 表达式
                this.filesArr.forEach((item)=>{
                    //将图片路径push到要上传的列表
                    let res=JSON.parse(item.response)
                 this.pic_list.push(res.data)
                })
                // 发送请求
                await this.$u.post('/place_order',{
                    orderSn:this.orderSn,
                    content:this.content,
                    pic_list:JSON.stringify(this.pic_list)
                }).then(res=>{
                    uni.showToast({
                        title:res.msg,
                    })
                    setTimeout(()=>{
                       uni.navigateBack({
                           delta:1
                       })
                    },2000)
                })
               
                
            },
            	onUploaded(lists) {
            				this.filesArr = lists;
            			},
                        // 图片上传失败
                        error(res) {
                            uni.showToast({
                                title: "上传遇到未知错误",
                                icon: 'none'
                            })
                        
                        },
                        // 图片过大
                        oversize() {
                            uni.showToast({
                                title: "图片过大，请重新上传",
                                icon: 'none'
                            })
                        },
        }
	}
